import React from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import '../App.css';
import './Home.css';
import Nav from "./Nav";

const Home = () => {
    return (
        <div id="home-main">
            <Nav />

            <div id="body" className="container">
                <h2 id="home-title">Microservice Security Smells</h2>
                <center>
                    <p id="smell-description">A microservice security smell can be observed in an application, as a possible symptom of a bad decision (often made unintentionally) that can negatively impact the overall application's security.
                        <br /> The effects of security smells can be resolved by refactoring the application or the services therein without altering the functionality provided to clients.</p>
                </center>
                <div id="research">
                    <h5>Links to research:</h5>
                    <div className="row lp-row">
                        <div id="mlr" className="col">
                            <a className="btn lp-btn" target="_blank" rel="noopener noreferrer" href="https://doi.org/10.1016/j.jss.2022.111393" role="button">Multivocal literature review</a>
                            <a className="btn lp-btn" target="_blank" rel="noopener noreferrer" href="https://doi.org/10.1007/978-3-030-71906-7_11" role="button">Towards resolving security smells</a>
                            <a className="btn lp-btn" target="_blank" rel="noopener noreferrer" href="https://doi.org/10.1007/978-3-031-16697-6_9" role="button">Impacts analysis (proposal)</a>
                        </div>
                    </div>
                </div>
            </div>

            <footer className="footer pt-4" id="footer">
                <div className="container">
                    <span className="text-muted">Copyright © 2023 Francisco Ponce</span>
                </div>
            </footer>

        </div>
    );
};

export default Home;